<template>
  <div class="map-editor w-100 h-100">
    <div v-if="preview" class="d-flex flex-column vw-100 vh-100">
      <div class="toolbar d-flex">
        <button class="btn btn-primary" @click="preview = false">Exit Preview</button>
        <button class="btn btn-primary mr-auto" type="button" @click="windowed = !windowed">Windowed</button>
      </div>
      <div class="windowed" v-if="windowed">
        <map-preview :map="map" />
      </div>
      <map-preview v-else class="flex-grow-1" :map="map" />
    </div>
    <div v-else class="d-flex flex-column vw-100 vh-100">
      <global-events
        @keyup.ctrl.90.prevent="undo"
        @keyup.ctrl.89.prevent="redo"
        @keyup.ctrl.88.prevent="removeSelected"
        @keyup.shift.78.prevent="setTool('select')"
        @keyup.shift.82.prevent="setTool('paintrooms')"
        @keyup.shift.80.prevent="setTool('paintpaths')"
        @keyup.shift.69.prevent="setTool('eraser')"
        @keyup.shift.70.prevent="setTool('formatpainter')"
      />
      <div class="toolbar d-flex p-1">
        <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="New map" @click="createNew"><i class="las la-file fa-fw"></i></button>
        <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Save" @click="save"><i class="las la-save fa-fw"></i></button>
        <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Load" @click="loadFromLocalStorage">
          <i class="las la-folder-open fa-fw"></i>
        </button>
        <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Download as JSON" @click="downloadJson">
          <i class="las la-file-export fa-fw"></i>
        </button>
        <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Download as SVG" @click="downloadSvg">
          <i class="las la-file-image fa-fw"></i>
        </button>

        <button class="btn btn-primary tool ml-2" v-b-tooltip.hover.bottom title="Undo" @click="undo" :disabled="!hasUndo">
          <i class="las la-undo fa-fw"></i>
        </button>
        <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Redo" @click="redo" :disabled="!hasRedo"><i class="las la-redo fa-fw"></i></button>

        <button
          class="btn btn-primary tool ml-2"
          v-b-tooltip.hover.bottom
          title="Map settings"
          @click="setTool('map-settings')"
          :class="{ active: currentTool == 'map-settings' }"
        >
          <i class="las la-cog fa-fw"></i>
        </button>

        <button
          class="btn btn-primary tool ml-2"
          v-b-tooltip.hover.bottom
          title="Select tool"
          @click="setTool('select')"
          :class="{ active: currentTool == 'select' }"
        >
          <i class="las la-mouse-pointer fa-fw"></i>
        </button>
        <button
          class="btn btn-primary tool"
          v-b-tooltip.hover.bottom
          title="Draw rooms"
          @click="setTool('paintrooms')"
          :class="{ active: currentTool == 'paintrooms' }"
        >
          <i class="las la-plus-square fa-fw"></i>
        </button>
        <button
          class="btn btn-primary tool"
          v-b-tooltip.hover.bottom
          titlie="Draw paths"
          @click="setTool('paintpaths')"
          :class="{ active: currentTool == 'paintpaths' }"
        >
          <i class="las la-road fa-fw"></i>
        </button>
        <button
          class="btn btn-primary tool"
          v-b-tooltip.hover.bottom
          title="Erase rooms"
          @click="setTool('eraser')"
          :class="{ active: currentTool == 'eraser' }"
        >
          <i class="las la-eraser fa-fw"></i>
        </button>
        <button
          class="btn btn-primary tool"
          v-b-tooltip.hover.bottom
          title="Style painter"
          @click="setTool('formatpainter')"
          :class="{ active: currentTool == 'formatpainter' }"
        >
          <i class="las la-brush fa-fw"></i>
        </button>
        <button class="btn btn-primary tool ml-2" v-b-tooltip.hover.bottom title="Show ids" @click="showIds = !showIds">
          <i class="las la-comment fa-fw"></i>
        </button>
        <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Show info" @click="showDebug = !showDebug">
          <i class="las la-info fa-fw"></i>
        </button>

        <button class="btn btn-primary tool ml-2" v-b-tooltip.hover.bottom title="Preview" @click="preview = true"><i class="las la-eye fa-fw"></i></button>
      </div>
      <div class="editor flex-grow-1">
        <map-preview
          ref="preview"
          :map="map"
          :show-cursor="true"
          :show-bounds="true"
          :selected-rooms="selectedRooms"
          :selected-paths="selectedPaths"
          :show-ids="showIds"
          :show-debug="showDebug"
          @cellclick="onCellClick"
          @roomclick="onRoomClick"
          @pathclick="onPathClick"
        />
      </div>
      <transition name="slide-fade">
        <div class="sidebar p-2" v-if="selectedRooms.length > 0 || selectedPaths.length > 0 || editMapSettings">
          <div class="container">
            <template v-if="selectedRooms.length > 1">
              <div class="d-flex align-items-center">
                <h4 class="mr-auto">Selected Rooms: {{ selectedRooms.length }}</h4>
                <button type="button" class="btn btn-link text-white btn-lg" @click="clearSelectedRooms"><i class="las la-times"></i></button>
              </div>

              <div class="row mb-2">
                <div class="toolbar d-flex">
                  <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Remove" @click="removeSelectedRooms">
                    <i class="las la-times fa-fw"></i>
                  </button>
                </div>
              </div>
            </template>
            <template v-else-if="selectedRoom != null">
              <div class="d-flex align-items-center">
                <h4 class="mr-auto">Room: {{ selectedRoom.id }}</h4>
                <button type="button" class="btn btn-link text-white btn-lg" @click="clearSelectedRooms"><i class="las la-times"></i></button>
              </div>

              <div class="row mb-2">
                <div class="toolbar d-flex">
                  <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Remove" @click="removeSelectedRooms">
                    <i class="las la-times fa-fw"></i>
                  </button>
                </div>
              </div>

              <div class="form-group row">
                <label class="col-sm-2 col-form-label">X</label>
                <input type="number" :min="0" :max="map.width" class="form-control col-sm-4" v-model.number="selectedRoom.x" />
                <label class="col-sm-2 col-form-label">Y</label>
                <input type="number" :min="0" :max="map.height" class="form-control col-sm-4" v-model.number="selectedRoom.y" />
              </div>
              <div class="form-group row">
                <label class="col-sm-2 col-form-label">Shape</label>
                <shape-input class="col-sm-10" v-model="selectedRoom.shape"/>
              </div>

              <div class="form-group row">
                <label class="col-sm-2 col-form-label">Color</label>
                <color-input class="col-sm-10" v-model="selectedRoom.color"/>
              </div>

              <div class="form-group row">
                <label class="col-sm-2 col-form-label">Icon</label>
                <input type="text" class="form-control col-sm-10" v-model="selectedRoom.icon" />
              </div>

              <div class="form-group">
                <label>Paths</label>
                <table class="table table-bordered directions-table bg-dark">
                  <tr>
                    <td style="width: 33%">
                      <button v-if="selectedRoomPaths.nw" class="btn btn-primary" @click="selectPath(selectedRoomPaths.nw)">
                        {{ selectedRoomPaths.nw.id }}
                      </button>
                    </td>
                    <td style="width: 33%">
                      <button v-if="selectedRoomPaths.n" class="btn btn-primary" @click="selectPath(selectedRoomPaths.n)">{{ selectedRoomPaths.n.id }}</button>
                    </td>
                    <td style="width: 33%">
                      <button v-if="selectedRoomPaths.ne" class="btn btn-primary" @click="selectPath(selectedRoomPaths.ne)">
                        {{ selectedRoomPaths.ne.id }}
                      </button>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <button v-if="selectedRoomPaths.w" class="btn btn-primary" @click="selectPath(selectedRoomPaths.w)">{{ selectedRoomPaths.w.id }}</button>
                    </td>
                    <td></td>
                    <td>
                      <button v-if="selectedRoomPaths.e" class="btn btn-primary" @click="selectPath(selectedRoomPaths.e)">{{ selectedRoomPaths.e.id }}</button>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <button v-if="selectedRoomPaths.sw" class="btn btn-primary" @click="selectPath(selectedRoomPaths.sw)">
                        {{ selectedRoomPaths.sw.id }}
                      </button>
                    </td>
                    <td>
                      <button v-if="selectedRoomPaths.s" class="btn btn-primary" @click="selectPath(selectedRoomPaths.s)">{{ selectedRoomPaths.s.id }}</button>
                    </td>
                    <td>
                      <button v-if="selectedRoomPaths.se" class="btn btn-primary" @click="selectPath(selectedRoomPaths.se)">
                        {{ selectedRoomPaths.se.id }}
                      </button>
                    </td>
                  </tr>
                </table>
              </div>
            </template>
            <template v-else-if="selectedPaths.length > 1">
              <div class="d-flex align-items-center">
                <h4 class="mr-auto">Selected paths: {{ selectedPaths.length }}</h4>
                <button type="button" class="btn btn-link text-white btn-lg" @click="clearSelectedPaths"><i class="las la-times"></i></button>
              </div>

              <div class="row mb-2">
                <div class="toolbar d-flex">
                  <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Remove" @click="removeSelectedPaths">
                    <i class="las la-times fa-fw"></i>
                  </button>
                </div>
              </div>
            </template>
            <template v-else-if="selectedPath">
              <div class="d-flex align-items-center">
                <h4 class="mr-auto">Path: {{ selectedPath.id }}</h4>
                <button type="button" class="btn btn-link text-white btn-lg" @click="selectedPath = null"><i class="las la-times"></i></button>
              </div>
              <div class="row mb-2">
                <div class="toolbar d-flex">
                  <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Remove" @click="removePath(selectedPath)">
                    <i class="las la-times fa-fw"></i>
                  </button>
                  <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Reverse" @click="reversePath(selectedPath)">
                    <i class="las la-retweet fa-fw"></i>
                  </button>
                </div>
              </div>

              <div class="form-group row">
                <label class="col-sm-2 col-form-label">Source</label>
                <input class="form-control col-sm-4" type="text" v-model="selectedPath.source" />
                <label class="col-sm-2 col-form-label">Target</label>
                <input class="form-control col-sm-4" type="text" v-model="selectedPath.target" />
              </div>
              <div class="form-group row">
                <label class="col-sm-2 col-form-label">Color</label>
                <color-input class="col-sm-10" v-model="selectedPath.color"/>
              </div>

              <div class="custom-control custom-checkbox">
                <input type="checkbox" class="custom-control-input" id="customCheck1" v-model="selectedPath.oneWay" />
                <label class="custom-control-label" for="customCheck1">One-way</label>
              </div>
            </template>
            <template v-else-if="editMapSettings">
              <div class="d-flex align-items-center">
                <h4 class="mr-auto">Map Settings</h4>
                <button type="button" class="btn btn-link text-white btn-lg" @click="editMapSettings = null"><i class="las la-times"></i></button>
              </div>
              <div class="row mb-2">
                <div class="toolbar d-flex">
                  <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Compact" @click="compactMap"><i class="las la-expand fa-fw"></i></button>
                  <button class="btn btn-primary tool" v-b-tooltip.hover.bottom title="Center" @click="centerMap">
                    <i class="las la-crosshairs fa-fw"></i>
                  </button>
                </div>
              </div>

              <div class="form-group row">
                <label class="col-sm-2 col-form-label">Width</label>
                <input type="number" min="0" max="1000" class="form-control col-sm-10" v-model.number="map.width" />
              </div>
              <div class="form-group row">
                <label class="col-sm-2 col-form-label">Height</label>
                <input type="number" min="0" max="1000" class="form-control col-sm-10" v-model.number="map.height" />
              </div>
              <div class="form-group row">
                <label class="col-sm-2 col-form-label">Color</label>
                <color-input class="col-sm-10" v-model="map.background"/>
              </div>
            </template>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>
<style>
.map-editor .btn-primary,
.map-editor .form-control {
  color: white;
  background-color: #475764;
  border-color: #000000;
}
</style>
<style scoped>
.windowed {
  width: 400px;
  height: 400px;
  margin-left: 100px;
  margin-top: 100px;
  border: 2px solid black;
  margin: 0 auto;
}

.vw-100 {
  width: 100vw;
}
.vh-100 {
  height: 100vh;
}
.fa-fw {
  text-align: center;
  width: 1.25em;
}
.toolbar {
  background: #262e36;
  border-bottom: 1px solid black;
}
.toolbar > button {
  margin-right: 0.25rem;
}
.toolbar .tool {
  padding: 0.2rem 0.4rem;
  text-align: center;
  font-size: 150%;
}
.sidebar {
  background: #262e36;
  width: 30rem;
  border-left: 1px solid black;
  overflow-y: auto;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
}

.slide-fade-enter-active {
  transition: all 0.5s ease;
}
.slide-fade-leave-active {
  transition: all 0.5s ease;
}
.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateX(30rem);
  opacity: 0;
}
.directions-table {
  padding: 0;
  margin: 0;
}
.directions-table td {
  border-color: black !important;
  padding: 0.1rem;
  width: 33%;
  height: 4rem;
}
.directions-table button {
  width: 100%;
  height: 100%;
}
.col-form-label {
  padding: calc(0.375rem + 1px);
}
</style>
<script>
import fileDownload from "js-file-download";
import MapPreview from "./MapPreview";
import MapRenderer from "./MapRenderer";
import ColorInput from "./ColorInput";
import ShapeInput from "./ShapeInput";
import UndoManager from "./undoManager";
import Pathfinder from "./pathfinder";
import GlobalEvents from "./GlobalEvents";

export default {
  components: {
    MapPreview,
    MapRenderer,
    ColorInput,
    ShapeInput,
    GlobalEvents
  },
  data() {
    return {
      preview: false,
      windowed: false,
      undoManager: new UndoManager(),
      hasUndo: false,
      hasRedo: false,
      isUndoing: false,
      map: null,
      editMapSettings: false,
      selectedPaths: [],
      selectedRooms: [],
      selectedRoomStyle: null,
      newPath: null,
      currentTool: "select",
      showIds: false,
      showDebug: false,
    };
  },
  watch: {
    map: {
      deep: true,
      handler: "onMapChanged",
    },
  },
  computed: {
    selectedRoom() {
      if (this.selectedRooms.length == 1) {
        var id = this.selectedRooms[0];
        return this.getRoomById(id);
      } else {
        return null;
      }
    },
    selectedPath() {
      if (this.selectedPaths.length == 1) {
        var id = this.selectedPaths[0];
        return this.getPathById(id);
      } else {
        return null;
      }
    },
    selectedRoomPaths() {
      var output = {
        n: null,
        ne: null,
        nw: null,
        s: null,
        se: null,
        sw: null,
        e: null,
        w: null,
        u: null,
        d: null,
      };
      if (this.selectedRoom == null) return output;
      var room = this.selectedRoom;
      var paths = this.map.paths.filter((m) => m.source == room.id || m.target == room.id);

      for (var path of paths) {
        var source = this.getRoomById(path.source);
        var target = this.getRoomById(path.target);
        if (!source || !target) continue;

        var direction = "";
        if (source.id != room.id) {
          var next = path.waypoints[1];

          if (source.y < next.y) {
            direction += "n";
          } else if (source.y > next.y) {
            direction += "s";
          }
          if (source.x < next.x) {
            direction += "w";
          } else if (source.x > next.x) {
            direction += "e";
          }
        } else {
          var next = path.waypoints[path.waypoints.length - 2];

          if (source.y < next.y) {
            direction += "s";
          } else if (source.y > next.y) {
            direction += "n";
          }
          if (source.x < next.x) {
            direction += "e";
          } else if (source.x > next.x) {
            direction += "w";
          }
        }
        output[direction] = path;
      }
      return output;
    },
  },
  created() {
    this.createNew();
  },
  methods: {
    zoom(direction) {
      var t = this.zoomer.getTransform();
      this.zoomer.smoothZoomAbs(t.x, t.y, t.scale + direction * 0.1);
    },
    resetZoom() {
      this.zoomer.smoothZoomAbs(this.initialTransform.x, this.initialTransform.y, this.initialTransform.scale);
    },
    onCellClick(e) {
      // if (this.currentTool == "select") {
      //   this.toggleRoomByCoordinates(e.x, e.y, e.shiftKey);
      // }
      if (this.currentTool == "paintrooms") {
        this.addRoom(e.x, e.y);
      }
      if (this.currentTool == "formatpainter") {
        this.paintRoom(e.x, e.y);
      }
      if (this.currentTool == "paintpaths") {
        this.paintPaths(e.x, e.y);
      }
      if (this.currentTool == "eraser") {
        this.erase(e.x, e.y);
      }
    },
    onRoomClick(evt) {
      if (this.currentTool == "select") {
        var room = this.getRoomById(evt.id);
        this.toggleRoom(room, evt.shiftKey);
      }
    },
    onPathClick(evt) {
      if (this.currentTool == "select") {
        var path = this.getPathById(evt.id);
        this.togglePath(path, evt.shiftKey);
      }
    },
    getRoomByCoordinates(x, y) {
      return this.map.rooms.find((m) => m.x == x && m.y == y);
    },
    getRoomById(id) {
      return this.map.rooms.find((m) => m.id == id);
    },
    getPathById(id) {
      return this.map.paths.find((m) => m.id == id);
    },
    setTool(name) {
      if (name == "formatpainter") {
        if (this.selectedRoom != null && this.tool != "formatpainter") {
          this.selectedRoomStyle = this.selectedRoom;
        } else {
          this.selectedRoomStyle = null;
        }
      }
      if (name == "map-settings") {
        this.editMapSettings = !this.editMapSettings;
      } else {
        this.currentTool = name;
      }

      this.clearSelectedRooms();
      this.clearSelectedPaths();

      if (this.newPath) {
        this.removePath(this.newPath);
        this.newPath = null;
      }
    },
    addRoom(x, y) {
      var room = this.getRoomByCoordinates(x, y);
      if (room) return;

      var shape = this.selectedRoomStyle != null ? this.selectedRoomStyle.shape : "square";
      var color = this.selectedRoomStyle != null ? this.selectedRoomStyle.color : "gray";
      var icon = this.selectedRoomStyle != null ? this.selectedRoomStyle.icon : null;

      room = { id: this.map.itemCounter++, x: x, y: y, shape: shape, color: color, icon: icon };
      this.map.rooms.push(room);

      this.selectRoom(room);
    },
    paintRoom(x, y) {
      var room = this.getRoomByCoordinates(x, y);
      if (!room) return;

      if (this.selectedRoomStyle == null) {
        this.selectedRoomStyle = room;
      } else {
        room.color = this.selectedRoomStyle.color;
        room.shape = this.selectedRoomStyle.shape;
        room.icon = this.selectedRoomStyle.icon;
      }
    },
    makeId(x, y) {
      return x + ":" + y;
    },
    addIfInRange(node, x, y, weight) {
      if (x < 0 || x >= this.map.width) return;
      if (y < 0 || y >= this.map.height) return;

      node[this.makeId(x, y)] = weight;
    },
    pathfind(x1, y1, x2, y2) {
      if (x1 == x2 && y1 == y2) return null;

      let graph = {};
      let maxDistance = 15;

      // The algorithm is not very efficient so i limit the max distance to 25 cells.
      // Otherwise it takes way to long to complete

      var startX = Math.max(0, x1 - maxDistance);
      var startY = Math.max(0, y1 - maxDistance);
      var endX = Math.min(this.map.width - 1, x1 + maxDistance);
      var endY = Math.min(this.map.height - 1, y1 + maxDistance);

      for (var y = startX; y <= endX; y++) {
        for (var x = startY; x <= endY; x++) {
          var node = {};
          this.addIfInRange(node, x - 1, y - 1, 1.414);
          this.addIfInRange(node, x, y - 1, 1);
          this.addIfInRange(node, x + 1, y - 1, 1.414);
          this.addIfInRange(node, x - 1, y, 1);
          this.addIfInRange(node, x + 1, y, 1);
          this.addIfInRange(node, x - 1, y + 1, 1.414);
          this.addIfInRange(node, x, y + 1, 1);
          this.addIfInRange(node, x + 1, y + 1, 1.414);
          graph[this.makeId(x, y)] = node;
        }
      }

      for (var path of this.map.paths) {
        var waypoints = path.waypoints;
        if (waypoints.length <= 1) continue;

        for (var i = 0; i < waypoints.length - 1; i++) {
          var from = waypoints[i];
          var to = waypoints[i + 1];
          var fromId = this.makeId(from.x, from.y);
          var toId = this.makeId(to.x, to.y);
          delete graph[fromId][toId];
          delete graph[toId][fromId];
        }
      }

      var pathfinder = new Pathfinder();
      var result = pathfinder.findShortestPath(graph, this.makeId(x1, y1), this.makeId(x2, y2));

      if (result.distance == "Infinity") return null;

      var path = result.path;
      var output = [];
      for (var i = 0; i < path.length; i += 1) {
        var point = path[i].split(":");
        output.push({
          x: parseInt(point[0]),
          y: parseInt(point[1]),
        });
      }
      return output;
    },
    paintPaths(x, y) {
      var room = this.getRoomByCoordinates(x, y);
      if (!room) {
        if (this.newPath != null) {
          var last = this.newPath.waypoints[this.newPath.waypoints.length - 1];
          var path = this.pathfind(last.x, last.y, x, y);
          if (path == null) return;
          for (var i = 1; i < path.length; i++) {
            var point = path[i];
            this.newPath.waypoints.push(point);
          }
        }
        return;
      }

      if (this.newPath != null) {
        this.newPath.target = room.id;

        var last = this.newPath.waypoints[this.newPath.waypoints.length - 1];
        var path = this.pathfind(last.x, last.y, x, y);
        if (path == null) return;
        for (var i = 1; i < path.length; i++) {
          var point = path[i];
          this.newPath.waypoints.push(point);
        }

        // if (this.map.paths.some((m) => (m.source == this.newPath.source && m.target == this.newPath.target) || (m.target == this.newPath.source && m.source == this.newPath.target))) {
        //   this.map.paths.
        // }
      }

      this.selectedRooms.splice(0, this.selectedRooms.length, room.id);

      var color = this.selectedRoomStyle ? this.selectedRoomStyle.color : "gray";
      this.newPath = {
        id: this.map.itemCounter++,
        color: color,
        source: room.id,
        target: null,
        waypoints: [{ x: room.x, y: room.y }],
        oneWay: false,
      };

      this.map.paths.push(this.newPath);
    },
    erase(x, y) {
      var room = this.getRoomByCoordinates(x, y);
      if (!room) return;
      this.removeRoom(room);
    },
    reversePath(path) {
      if (!path) return;
      var temp = path.target;
      path.target = path.source;
      path.source = temp;
      path.waypoints.reverse();
    },
    createNew() {
      this.isUndoing = true;
      this.map = {
        width: 50,
        height: 50,
        background: "transparent",
        rooms: [],
        paths: [],
        itemCounter: 0,
      };
      this.setTool("map-settings");

      this.$nextTick(() => {
        this.isUndoing = false;
        this.undoManager.clear();
        this.undoManager.push(this.map);
      });
    },
    save() {
      localStorage.setItem("map", JSON.stringify(this.map));
    },
    setMap(map) {
      this.isUndoing = true;
      this.map = map;
      this.$nextTick(() => {
        this.isUndoing = false;
        this.undoManager.clear();
        this.undoManager.push(this.map);
      });
    },
    loadFromLocalStorage() {
      var retrievedObject = localStorage.getItem("map");
      if (retrievedObject) {
        var map = JSON.parse(retrievedObject);
        this.setMap(map);
      } else if (this.map == null) {
        this.createNew();
      }
    },
    downloadJson() {
      var map = JSON.stringify(this.map, null, 4);
      fileDownload(map, "map.json");
    },
    downloadSvg() {
      var map = new XMLSerializer().serializeToString(this.$refs.preview.$refs.canvas.$el);
      fileDownload(map, "map.svg");
    },
    onMapChanged() {
      if (this.isUndoing) return;

      this.undoManager.push(this.map);

      this.refreshUndoState();
    },
    refreshUndoState() {
      this.hasUndo = this.undoManager.hasUndo();
      this.hasRedo = this.undoManager.hasRedo();
    },
    undo() {
      this.undoManager.undo((map) => {
        this.isUndoing = true;
        this.map = map;
        this.$nextTick(() => {
          this.isUndoing = false;
        });
      });
      this.refreshUndoState();
    },
    redo() {
      this.undoManager.redo((map) => {
        this.isUndoing = true;
        this.map = map;
        this.$nextTick(() => {
          this.isUndoing = false;
        });
      });
      this.refreshUndoState();
    },

    toggleRoomByCoordinates(x, y, shiftKey) {
      var room = this.getRoomByCoordinates(x, y);
      this.toggleRoom(room, shiftKey);
    },
    isRoomSelected(room) {
      return this.selectedRooms.includes(room.id);
    },
    selectRoom(room, shiftKey) {
      if (!room) return;

      if (this.selectedRooms.length > 0 && shiftKey) {
        if (this.isRoomSelected(room)) return;
        this.selectedRooms.push(room.id);
        return;
      } else {
        this.selectedRooms.splice(0, this.selectedRooms.length, room.id);
      }
      this.selectedRoomStyle = room;
      this.clearSelectedPaths();
    },
    deselectRoom(room) {
      if (!room) return;

      var idx = this.selectedRooms.indexOf(room.id);
      if (idx != -1) {
        this.selectedRooms.splice(idx, 1);
      }
    },
    toggleRoom(room, shiftKey) {
      if (!room) return;
      if (!this.isRoomSelected(room)) {
        this.selectRoom(room, shiftKey);
      } else {
        this.deselectRoom(room);
      }
    },
    clearSelectedRooms() {
      this.selectedRooms.splice(0, this.selectedRooms.length);
    },
    removeRoom(room) {
      var idx = this.map.rooms.indexOf(room);
      if (idx != -1) {
        this.map.rooms.splice(idx, 1);
      }

      for (var i = this.map.paths.length - 1; i >= 0; i--) {
        var path = this.map.paths[i];
        if (path.source == room.id || path.target == room.id) {
          this.removePath(path);
        }
      }

      this.deselectRoom(room);
    },
    removeSelectedRooms() {
      for(var i = this.selectedRooms.length -1 ; i >= 0; i--){
        var id = this.selectedRooms[i];
        var room = this.getRoomById(id);
        this.removeRoom(room);
      }
      this.clearSelectedRooms();
    },
    isPathSelected(path) {
      return this.selectedPaths.includes(path.id);
    },
    selectPath(path, shiftKey) {
      if (!path) return;

      if (this.selectedPaths.length > 0 && shiftKey) {
        if (this.isPathSelected(path)) return;
        this.selectedPaths.push(path.id);
        return;
      } else {
        this.selectedPaths.splice(0, this.selectedPaths.length, path.id);
      }
      this.clearSelectedRooms();
    },
    deselectPath(path) {
      if (!path) return;

      var idx = this.selectedPaths.indexOf(path.id);
      if (idx != -1) {
        this.selectedPaths.splice(idx, 1);
      }
    },
    togglePath(path, shiftKey) {
      if (!path) return;
      if (!this.isPathSelected(path)) {
        this.selectPath(path, shiftKey);
      } else {
        this.deselectPath(path);
      }
    },
    clearSelectedPaths() {
      this.selectedPaths.splice(0, this.selectedPaths.length);
    },
    removePath(path) {
      var idx = this.map.paths.indexOf(path);
      if (idx != -1) {
        this.map.paths.splice(idx, 1);
      }
      this.deselectPath(path);
    },

    removeSelectedPaths() {
      for(var i = this.selectedPaths.length -1 ; i >= 0; i--){
        var id = this.selectedPaths[i];
        var path = this.getPathById(id);
        this.removePath(path);
      }
      this.clearSelectedPaths();
    },
    removeSelected(){
      this.removeSelectedRooms();
      this.removeSelectedPaths();
    },

    compactMap() {
      var minX = null;
      var minY = null;
      var maxX = null;
      var maxY = null;

      for (var room of this.map.rooms) {
        minX = minX != null ? Math.min(minX, room.x) : room.x;
        minY = minY != null ? Math.min(minY, room.y) : room.y;
        maxX = maxX != null ? Math.max(maxX, room.x) : room.x;
        maxY = maxY != null ? Math.max(maxY, room.y) : room.y;
      }

      for (var path of this.map.paths) {
        for (var point of path.waypoints) {
          minX = minX != null ? Math.min(minX, point.x) : point.x;
          minY = minY != null ? Math.min(minY, point.y) : point.y;
          maxX = maxX != null ? Math.max(maxX, point.x) : point.x;
          maxY = maxY != null ? Math.max(maxY, point.y) : point.y;
        }
      }

      if (minX == null || minY == null || maxX == null || maxY == null) {
        return;
      }

      var width = maxX - minX;
      var height = maxY - minY;
      var offsetX = -minX;
      var offsetY = -minY;
      for (var room of this.map.rooms) {
        room.x += offsetX;
        room.y += offsetY;
      }

      for (var path of this.map.paths) {
        for (var point of path.waypoints) {
          console.log(point);
          point.x += offsetX;
          point.y += offsetY;
        }
      }

      this.map.width = width + 1;
      this.map.height = height + 1;
    },
    centerMap() {
      var minX = null;
      var minY = null;
      var maxX = null;
      var maxY = null;

      for (var room of this.map.rooms) {
        minX = minX != null ? Math.min(minX, room.x) : room.x;
        minY = minY != null ? Math.min(minY, room.y) : room.y;
        maxX = maxX != null ? Math.max(maxX, room.x) : room.x;
        maxY = maxY != null ? Math.max(maxY, room.y) : room.y;
      }

      for (var path of this.map.paths) {
        for (var point of path.waypoints) {
          minX = minX != null ? Math.min(minX, point.x) : point.x;
          minY = minY != null ? Math.min(minY, point.y) : point.y;
          maxX = maxX != null ? Math.max(maxX, point.x) : point.x;
          maxY = maxY != null ? Math.max(maxY, point.y) : point.y;
        }
      }

      if (minX == null || minY == null || maxX == null || maxY == null) {
        return;
      }

      var width = maxX - minX + 1;
      var height = maxY - minY + 1;

      var offsetX = Math.round((this.map.width - width) / 2) - minX;
      var offsetY = Math.round((this.map.width - height) / 2) - minY;

      for (var room of this.map.rooms) {
        room.x += offsetX;
        room.y += offsetY;
      }

      for (var path of this.map.paths) {
        for (var point of path.waypoints) {
          point.x += offsetX;
          point.y += offsetY;
        }
      }
    },
  },
};
</script>
