<template>
  <div class="p-0">
    <select :value="value" @change="updateValue($event.target.value)" class="form-control">
        <option v-for="(item, i) in predefinedShapes" :key="i" :value="item.value">
            {{item.label}}
        </option>
    </select>
  </div>
</template>
<script>
export default {
  props: {
    value: {
      default: null,
    },
    predefinedShapes: {
        type: Array,
        default: () => [
           {value: 'square', label: 'Rectangle'},
           {value: 'circle', label: 'Circle'},
           {value: 'pentagon', label: 'Pentagon'},
           {value: 'hexagon', label: 'Hexagon'},
           {value: 'octagon', label: 'Octagon'},
           {value: 'star', label: 'Star'},
           {value: 'hourglass', label: 'Hourglass'},
           {value: 'trapezoid', label: 'Trapezoid'},
        ]
    }
  },
  methods: {
    updateValue(value) {
      this.$emit("input", value);
    },
  },
};
</script>
